:root {
    --transitionTime: 0ms;
    --transitionType: linear;
}

#ColorTool{
    background-color: #fff;
    /*Height is based on app's Navigation-Bar height settings*/
    height: calc(100vh - 36pt);
    max-height: calc(100vh - 6vmin);
    min-width: 100%;
}

p{
    margin: 0;
}

/* Canvas */
.ColorTool-Container{
    --padding: 0vh;
    display: flex;
    flex-direction: row;
    height: calc(100% - var(--padding) * 2);
    width: auto;
    padding: var(--padding);
    background-color: rgba(0,0,0,0.16);
}

.Canvas-Container{
    
    position: relative;
    height: 100%;
    width: 100%;
    display: flex;
    align-content: center;

    z-index: 1;
    
}

.CT-Img-Container{
    background-color: #263238;
    padding: 6vh;
}

.Layer{
    height: 100%;
    width: auto;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

/* Preview Layers */
#preview-main-png {
    z-index: 901;
    filter: drop-shadow(-1rem 1rem 3rem rgba(0, 0, 0, 0.45));
}
#preview-png-bag {
    z-index: 903; 
    mix-blend-mode: multiply;
}
#preview-png-sub-color{
    z-index: 903; 
    mix-blend-mode: multiply;
}

#preview-svg-bag {   
    z-index: 902;
}
#preview-svg-light {   
    z-index: 902;
}
#preview-svg-dark {   
    z-index: 902;
}



/* Button Field CSS */
.Color-Tabs-Controller{

    background-color: #fff;

    position: relative;
    height: 100%;
    min-height: 170px;
    
    display: flex;
    flex-flow: column;

    z-index: 910;

}

.Grid{
    display: grid;
    grid-template-columns: auto auto auto auto auto auto auto auto auto auto auto auto auto auto;
}
.Scroll{
    overflow-y: scroll;
    border-style: solid;
    border-color: rgba(0, 0, 0, 0.06);
    border-width: 0.25px;
}
.Tab{
    cursor: pointer;
    width: 33px;
    padding-top: 33px;
    margin: 0.5px;
    background-color: #fff;
}
.Tab:hover{
    border-radius: 20%;
}

/* Schemes */
#Scheme-Controller {
    flex: 0 1 342px;

    display: flex;
    flex-direction: row;
    width: 100%;
}
/* Script Related */
.selected {
    filter: drop-shadow(0px 0px 10px rgba(0, 0, 0, 0.6));
}