.scheme-container {
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    height: 372px;
    width: 237px;
    padding: 16px;
    border-style: solid;
    border-color: rgba(0, 0, 0, 0.68);
    border-width: 0px;
    border-right-width: 1px;
    display: flex;
    flex-wrap: wrap;
}

.scheme-container-title {
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    width: 100%;
    height: 21px;
    font-family: 'Roboto', sans-serif;
    font-weight: 400;
    color: rgba(0, 0, 0, 0.68);
    font-size: 13px;
    line-height: 13px;
    padding-bottom: 8px;
}

.scheme-container-content {
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    position: relative;
    width: 204px;
    height: 204px;
    padding: 8px;
    border-style: solid;
    border-color: rgba(0, 0, 0, 0.68);
    border-width: 1px;
    color: rgba(0, 0, 0, 0.68);
}

.scheme-container-content-hex {
    font-family: 'Roboto', sans-serif;
    font-weight: 700;
    font-size: 14px;
    line-height: 19px;
}

.scheme-container-content-font-color {
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 30px;
    height: 30px;
    border-radius: 100%;
    font-family: 'Roboto', sans-serif;
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;
}

.scheme-container-subcontent {
    width: 204px;
    height: 115px;
    background-color: white;
    color: rgba(0, 0, 0, 0.68);
    display: flex;
    flex: row;
}

.scheme-container-subcontent-small {
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    padding: 8px;
    border-style: solid;
    border-color: rgba(0, 0, 0, 0.68);
    border-width: 1px;
    border-top: 0px;
}

.scheme-container-subcontent-title {
    font-family: 'Roboto', sans-serif;
    font-weight: 400;
    font-size: 13px;
    line-height: 19px;
}

.scheme-container-subcontent-hex {
    font-family: 'Roboto', sans-serif;
    font-weight: 700;
    font-size: 13px;
    line-height: 19px;
}