.DoDont-grid-s {
    display: grid;
    grid-template-columns: 1fr 1fr;
    width: 800px;
    margin: auto;
}

.DoDont-s {
    display: inline-flex;
    flex-direction: column;
    margin: 0;
    margin-block: 1.25rem;

    width: 360px;
    padding-inline: 20px;

    background-color: white;
}

.DoDont-grid-l {
    display: grid;
    grid-template-columns: 1fr;
    width: 800px;
    margin: auto;
}

.DoDont-l {
    display: inline-flex;
    flex-direction: column;
    margin: 0;
    margin-block: 1.25rem;

    width: 760px;
    padding-inline: 20px;

    background-color: white;
}

.DoDont-Img-Container {
    box-sizing: border-box;
    border: 1px solid rgba(0, 0, 0, 0.12);

    width: 100%;
    height: 360px;
    overflow: hidden;
}

.DoDont-Info {
    box-sizing: border-box;
    border-top: 15px solid;
    border-top-color: currentColor;

    display: block;
    line-height: 1.25rem;
    font-size: 0.875rem;

    margin-top: 1rem;

}

.DoDont-Title {
    display: block;
    margin: 0;
    padding: 10px 0 2px;
    font-weight: 500;
    color: currentColor;
}

.DoDont-Description {
    color: rgb(97, 97, 97);
}