.navigation-bar {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  

  z-index: 1000;
  position: fixed;
  top: 0;
  left: 0;

  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  
  font-family: 'Roboto', sans-serif !important;
  color: white;

  background-color: rgba(255, 255, 255, 0.11);
}

.navigation-bar-logo-container{
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  padding: 16px;

  display: flex;
  align-items: center;

  height: 100%;
}

.navigation-bar-logo{

  height: 100%;
  display: flex;
  align-items: center;
  margin-left: 16px;
}

.navigation-bar-links-container {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  padding-left: 16px;
  padding-right: 16px;

  height: 100%;
  width: 100%;

  display: flex;
  justify-content: flex-end;
  align-items: center;

  font-weight: 400;
}

.navigation-bar-link {
  padding-left: 16px;
  padding-right: 16px;

  display: flex;
  align-items: center;

  height: 100%;
  cursor: pointer;
}

.navigation-bar-link:hover {  
  background-color: rgba(255, 255, 255, 0.11);
}

.NavSpacer {
  min-height: 36pt;
  height: 6vmin;
}







.Nav-Size {
  min-height: 36pt;
  height: 6vmin;
  min-width: 100%;
}

a:visited {
  text-decoration: none;
  color: #fff;
}

a {
  text-decoration: none;
  /* no underline */
}

.black {
  background-color: black;
}